<template>
    <div>
        <Tip :title="type" class="f-border-b"></Tip>
        <Form :model="formItem" label-position="right" :label-width="100" ref="formValidate" :rules="ruleValidate"
              style="width:50%;margin-left:20%;">

            <FormItem label="姓名" prop="studentId">
                <Select v-model="formItem.studentId"  filterable>
                    <Option v-for="item in studentList" :value="item.studentId" :key="item.studentId">{{item.studentName}}</Option>
                </Select>
            </FormItem>
            <FormItem label="缴费金额" prop="payMoney">
                <Input v-model="formItem.payMoney" placeholder="缴费金额">
                    <Icon type="logo-yen" slot="prefix" />
                </Input>
            </FormItem>

            <FormItem label="缴费时间" prop="payDate">
                <Date-picker type="date" v-model="formItem.payDate" placeholder="选择日期"></Date-picker>
            </FormItem>
            <FormItem label="缴费课次" prop="payLessonNumber">
                <Input v-model.number="formItem.payLessonNumber"  placeholder="缴费课次"/>
            </FormItem>

            <FormItem label="下次付费日期" prop="nextPayDate">
                <Date-picker type="date" v-model="formItem.nextPayDate" placeholder="选择日期"></Date-picker>
            </FormItem>
            <FormItem>
                <Button type="primary" @click="submit('formValidate')">提交</Button>
                <Button type="primary" @click="goBack" style="margin-left: 8px">返回</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
    import _apiitems from '@/api/items'
    import Tip from '@/components/Tip'

    export default {
        name: "paymentrecord-view",
        props: {
            type: {
                type: String
            },
            title: {
                type: String
            },
            paymentId: {
                type: String,
                default: ''
            },
        },
        components: {
            Tip,
        },
        data() {
            return {
                selectStudentName:'',
                studentList:[],
                student:{
                    studentName:""
                },
                show: false,
                formItem: {
                    studentId: '',
                    payMoney: 0,
                    payDate: '',
                    payLessonNumber: 0,
                    nextPayDate: '',

                },
                ruleValidate: {
                    studentId: [
                        {required: true, message: "请选择学生", trigger: 'blur'}
                    ],
                    payMoney: [
                        {required: true, message: "请输入缴费金额", trigger: 'blur'}
                    ],
                },
            }
        },
        computed: {},
        mounted() {
            this.queryAllStudentList();
            this.getDates();
        },
        created() {
            if (this.title === 'add') {
                this.show = true
            } else if (this.title === 'update') {
                _apiitems.selectPaymentRecordByPK(this.paymentId).then(res => {
                    this.formItem = res.data;
                    this.show = true
                })
            }
        },
        methods: {
            getDates() {
                const time = new Date()
                const month = time.getMonth() + 1
                const day = time.getDate()
                const nowTime =
                    time.getFullYear() +
                    '-' +
                    (month < 10 ? '0' + month : month) +
                    '-' +
                    (day < 10 ? '0' + day : day)
                this.$data.formItem.payDate = nowTime;
            },
            queryAllStudentList() {
                let that = this;
                _apiitems.selectAllStudents(this.$data.student).then(res => {
                    that.$data.studentList = res.data;
                }).catch(err => {
                    console.log(err)
                    this.$Notice.error({title: '查询学生失败!'})
                });
            },
            submit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        if (this.title === 'add') {
                            _apiitems.addPaymentRecord({...this.formItem}).then(res => {
                                if (res) {
                                    this.$Notice.success({title: '新增成功!'})
                                    this.$emit('childFn', 'list');
                                }
                            }).catch(err => {
                                this.$Notice.error({title: '新增失败!'})
                            })
                        } else {
                            _apiitems.updatePaymentRecord({...this.formItem}).then(res => {
                                if (res) {
                                    this.$Notice.success({title: '修改成功!'})
                                    this.$emit('childFn', 'list');
                                }
                            }).catch(err => {
                                this.$Notice.error({title: '修改失败!'})
                            })
                        }
                    } else {
                        this.$Notice.error({title: '有数据未填写!'})
                    }
                })
            },
            goBack() {
                this.$emit('childFn', 'list');
            },
        }
    }
</script>

