<style scoped>
    .layout {
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }

    .layout-header-bar {
        background: #fff;
        box-shadow: 0 1px 1px rgba(0, 0, 0, .1);
    }
</style>
<!-- iview 3.5.3 -->
<template>
    <div class="layout" id="layout">
        <Sider :style="{position: 'fixed', height: '100vh', left: 0, overflow: 'auto'}">
            <Menu active-name="1-1" theme="dark" width="auto" :open-names="['1']" @on-select="lessonComponent">
                <Submenu name="1">
                    <template slot="title">
                        <Icon type="ios-navigate"></Icon>
                        学生管理
                    </template>
                    <menu-item name="1-1">学生管理</menu-item>
                </Submenu>
                <Submenu name="2">
                    <template slot="title">
                        <Icon type="ios-keypad"></Icon>
                        付费管理
                    </template>
                    <menu-item name="2-1">付费管理</menu-item>
                </Submenu>
                <Submenu name="3">
                    <template slot="title">
                        <Icon type="ios-analytics"></Icon>
                        数据分析
                    </template>
                    <menu-item name="3-1">A分析</menu-item>
                    <menu-item name="3-2">B分析</menu-item>
                </Submenu>
                <Submenu name="4">
                    <template slot="title">
                        <Icon type="ios-analytics"></Icon>
                        配置管理
                    </template>
                    <menu-item name="4-1">分类管理</menu-item>
                </Submenu>
            </Menu>
        </Sider>
        <Layout :style="{marginLeft: '200px'}">
            <Header :style="{background: '#fff', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}"></Header>
            <Content :style="{padding: '0 16px 16px'}">
                <Breadcrumb :style="{margin: '16px 0'}">
                    <BreadcrumbItem>IOU</BreadcrumbItem>
                    <BreadcrumbItem>My</BreadcrumbItem>
                    <BreadcrumbItem>Darling</BreadcrumbItem>
                </Breadcrumb>
                <Card>
                    <Students v-if="menu_name == '1-1'" @childFn="parentFn"></Students>
                    <Pay v-if="menu_name == '2-1'" @childFn="parentFn"></Pay>
                </Card>
            </Content>
        </Layout>
    </div>
</template>
<script>
    import Students from '@/views/students/students-list'
    import Pay from '@/views/paymentrecord/paymentrecord-list'
    export default {
        data (){
          return {
              menu_name: "1-1"  //跳转菜单初始化值
          }
        },
        components: {
            Students,
            Pay
        },
        methods: {
            lessonComponent(menuName) {
                this.$data.menu_name = menuName;   //点击菜单控制指定菜单显隐
            },
            parentFn(pageName) {
                this.$data.menu_name = ''
                setTimeout(()=>{
                    this.$data.menu_name = pageName
                },1)

            }
        }
    }
</script>
